import React from 'react'
import "./typesbtn.css"
import { Link } from "react-router-dom";


export default function Typesbtn(props) {
    return (
        <Link to={props.link}>
        <div className="type">
            <img src={props.icon} alt={props.title} />
            <div className="text">
                <h2>{props.title}</h2>
                <p>{props.dis}</p>
            </div>
        </div>
        </Link>
    )
}
